<template>
  <div class="wrapper">
    <div
        style="margin: 70px auto; background-color: #fff; width: 350px; height: 500px; padding: 20px; border-radius: 10px">
      <div style="margin: 5px  0; text-align: center; font-size: 24px"><b>注 册</b></div>
      <el-form :model="user" :rules="rules" ref="userForm" >
        <el-form-item prop="username">
          <el-input placeholder="请输入注册用户名" size="medium" prefix-icon="el-icon-user" v-model="user.username"></el-input>
        </el-form-item>
        <el-form-item prop="nickname">
          <el-input placeholder="请输入昵称" size="medium" prefix-icon="el-icon-user" v-model="user.nickname" ></el-input>
        </el-form-item>

        <el-form-item prop="password">
          <el-input size="medium" placeholder="请输入用户密码" prefix-icon="el-icon-lock" show-password v-model="user.password" ></el-input>
        </el-form-item>

        <el-form-item prop="confirm_password">
          <el-input size="medium" placeholder="请确认用户密码" prefix-icon="el-icon-lock" show-password v-model="user.confirm_password"></el-input>
        </el-form-item>

        <el-form-item prop="email">
          <el-input  size="medium" placeholder="请输入邮箱" prefix-icon="el-icon-message" v-model="user.email" ></el-input>
        </el-form-item>

        <el-form-item prop="phone">
          <el-input size="medium" placeholder="请输入电话" prefix-icon="el-icon-phone" v-model="user.phone" ></el-input>
        </el-form-item>
        <el-form-item prop="address">
          <el-input size="medium" placeholder="请输入地址" prefix-icon="el-icon-postcard" v-model="user.address"></el-input>
        </el-form-item>

        <el-form-item style="margin: 10px 0; text-align: right">
          <el-button prop="username" type="warning" size="small" autocomplete="off" @click="register()">
            注册
          </el-button>
          <el-button prop="password" type="primary" size="small" autocomplete="off" @click="$router.push('/login')">返回登录</el-button>
        </el-form-item>


      </el-form>
    </div>
  </div>
</template>

<script>
import {setRoutes} from "@/router";

export default {
  name: "Login",
  data() {
    return {
      user: {},
      rules: {
        username: [
          {required: true, message: '请输入注册用户名', trigger: 'blur'},
          {min: 1, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur'}
        ],
        nickname: [
          {required: true, message: '请输入注册用户名', trigger: 'blur'},
          {min: 1, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '请输入用户密码', trigger: 'blur'},
          {min: 1, max: 20, message: '长度在 3 到 5 个字符', trigger: 'blur'}
        ],
        confirm_password: [
          {required: true, message: '请确认用户密码', trigger: 'blur'},
          {min: 1, max: 20, message: '长度在 3 到 5 个字符', trigger: 'blur'}
        ],
        email: [
          {
            required: true,
            message: "请输入邮箱",
            trigger: "blur"
          },
          {
            validator: function(rule, value, callback) {
              if (/^\w{1,64}@[a-z0-9\-]{1,256}(\.[a-z]{2,6}){1,2}$/i.test(value) == false
              ) {
                callback(new Error("邮箱格式错误"));
              } else {
                callback();
              }
            },
          trigger: "blur"}
        ],
        phone: [
          {
            required: true,
            message: "请输入手机号码",
            trigger: "blur"
          },
          {
            validator: function(rule, value, callback) {
              if (/^1[34578]\d{9}$/.test(value) == false) {
                callback(new Error("手机号格式错误"));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        address: [
          {required: true, message: '请输入地址', trigger: 'blur'},
          {min: 1, max: 20, message: '长度在 3 到 5 个字符', trigger: 'blur'}
        ],
  },


    }
  },
  methods: {
    register() {
      if (this.user.confirm_password==this.user.password){
      console.log("进入register方法")
      this.$refs["userForm"].validate((valid) => {
        console.log("进入register方法1")
          if (valid) {//表单校验合法并且两次密码输入正确
            console.log("进入register方法3")
            this.request.post("/user/register", this.user).then(res => {
              if (res.code==200) {
                this.$message.success("注册成功")
                this.$router.push("/login")//注册成功，返回登录界面
              } else {
                this.$message.error(res.msg)
              }
            })
          }

      }
      )
      }else {
             this.$message.warning("两次输入的密码不一致，请重新输入")
             this.user.password = ""
             this.user.confirm_password = ""
      }
    }
  },
}

</script>

<style scoped>
.wrapper {
  height: 100vh;
  background-image: linear-gradient(to bottom right, #FC466B, #3F5EFB);
  overflow: hidden;
}
</style>
